<template>
    <div id="container">
        <b-steps v-model="activeStep">

            <!-- START NEW GAME -->
            <b-step-item>
                <div class="title is-5">
                    Select players:
                </div>
                <b-field v-for="player in allPlayers" :key="player">
                    <b-checkbox-button v-model="playersNewGame"
                        :native-value="player"
                        :type="playersNewGame.includes(player)?'is-success':'is-danger'">
                        <b-icon :icon="playersNewGame.includes(player)?'check':''" />
                        {{player}}
                        <b-icon />
                    </b-checkbox-button>
                </b-field>
                <b-field label="New Player" v-if="isAddPlayer">
                    <b-input ref="newPlayer" v-model="newPlayer" required/>
                </b-field>
                <b-button label="Confirm" v-if="isAddPlayer" @click="this.addPlayer" />
                <b-button :label="isAddPlayer?'Cancel':'Add Player'" @click="isAddPlayer=!isAddPlayer"/>
                <hr>
                    <b-button @click="startNewGame">Start Game</b-button>
                    <b-button @click="activeStep=1">Cancel</b-button>
            </b-step-item>
            <!-- END NEW GAME -->

            <!-- START SCORE PAGE -->
            <b-step-item>
                <div v-for="player in players" :key="player" ref="allplayers">
                    <div class="title is-4">{{player.name}}</div>
                    <div class="subtitle is-5">
                        Points: {{player.points}}<br>
                        Hand: {{player.hand}}
                    </div> 
                    <br>  
                </div>
                <b-collapse v-if="history.length > 0" class="panel" animation="slide" v-model="isHistoryOpen">
                    <template #trigger>
                        <div role="button" class="title is-6" style="padding:10px">
                            History
                        </div>
                    </template>
                    <div class="panel">
                        <div v-for="item in history" :key="item">
                            <hr>
                            <div v-for="loser in item" :key="loser">
                                {{loser}}
                            </div>
                            <br>    
                        </div>
                    </div>
                </b-collapse>
                
                <hr>
                <b-button @click="activeStep=0">New Game</b-button>
                <b-button @click="activeStep=2">Round Over</b-button>                
            </b-step-item>
            <!-- END SCORE PAGE -->

            <!-- START ROUND OVER -->
            <b-step-item>
                <div class="title is-5">Winner:</div>
                <b-field v-for="player in players" :key="player">
                    <!-- <div> -->
                        <b-radio-button v-model="winner" :native-value="player.name"
                            :type="winner===player.name?'is-success':'is-danger'">
                            <b-icon :icon="winner===player.name?'check':'close'" />
                            {{player.name}}
                        </b-radio-button>
                        
                    <!-- </div> -->
                </b-field>
                <div v-show="winner">
                    <div v-for="player in losers" :key="player">
                        <b-field :label="player.name">
                            <b-numberinput type="is-danger" v-model="player.roundPoints" placeholder=0 required />
                        </b-field>
                    </div>
                </div>
                <hr>
                <b-button @click="update">Update</b-button>
                <b-button @click="activeStep=1">Cancel</b-button>
            </b-step-item>
            <!-- END ROUND OVER -->

        </b-steps>
    </div>
</template>

<script>
export default {
    name: 'App',
    components: {
    },
    data() {
        return {
            activeStep: 1,
            allPlayers: ['Bryan','Dina','Chuey'],
            players: [
                {name:'Bryan',points:0,hand:100},
                {name:'Dina',points:0,hand:100},
            ],
            winner: '',
            losersList: [],
            history: [],
            historyId: 0,
            isHistoryOpen: false,
            playersNewGame: [],
            isAddPlayer: false,
            newPlayer: '',
        }
    },
    computed: {
        losers() {
            let losers = []
            this.players.forEach(el => {
                if (el.name!=this.winner)
                losers.push({name:el.name,roundPoints:0})
            });
            return losers
        },
    },
    methods: {
        addPlayer() {
            if (this.$refs.newPlayer.checkHtml5Validity()) {
                this.allPlayers.push(this.newPlayer)
                this.newPlayer = ''
                this.isAddPlayer = false
            }
            this.$refs.newPlayer.checkHtml5Validity()
        },
        startNewGame() {
            let newGame = []

            this.playersNewGame.forEach(el => {
                newGame.push({name:el,points:0,hand:100})
            });

            this.players = newGame
            this.history = []
            this.historyId = 0
            this.activeStep=1
        },
        update() {
            this.updatePoints()
            this.updateHistory()
            this.activeStep=1
            this.winner = ''
        },
        updatePoints() {
            for (let i = 0; i < this.players.length; i++) {
                this.losers.forEach(el => {
                    if (el.name===this.players[i].name) {
                        this.players[i].points += Number(el.roundPoints)
                        this.players[i].hand -= Number(el.roundPoints)
                    }
                });                
            }
        },
        updateHistory() {
            let round = []

            this.losers.forEach(el => {
                round.push(el.name + ': ' + el.roundPoints)
            });

            this.history.push(round)
        },
    },
    watch: {
        isAddPlayer: function() {
            if (this.isAddPlayer) {
                this.$nextTick(() => {
                    this.$refs.newPlayer.focus()
                })
            }
        }
    }
}
</script>

<style>
#container {
    text-align: center;
    max-width: 500px;
    margin: auto;
}

.button {
    margin: 10px;
}
</style>